.modal {
  opacity: 0;
  width: 30vw;
  height: 40vh;
  margin: 0 auto;
  border: 1px solid black;
  border-radius: 20px;
  background-color: aliceblue;
  color: black;
  padding: 0 10px;
  transition: 1s;
}

.block {
  opacity: 100%;
  transition: 1s;
  z-index: 3;
}

.modal h1 {
  margin-top: 4%;
  text-align: center;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.close {
  text-align: end;
}
.close span {
  text-align: center;
  cursor: pointer;
}
.close span:active {
  font-size: 26px;
}

.button {
  transition: 1s;
  position: absolute;
  z-index: 2;
  cursor: pointer;
  width: 150px;
  height: 50px;
  font-size: 18px;
  border-radius: 20px;
  box-shadow: 4px 4px 5px rgb(88, 2, 2);
  border: none;
  background-color: brown;
  color: aliceblue;
}

.btnNone {
  transition: 1s;
  opacity: 0;
}

.shkala {
  margin-left: 4px;
  height: 1vh;
  border: 1px solid rgb(58, 55, 55);
  border-radius: 10px;
}

.lvl {
  height: 100%;
  width: 0;
  background-color: brown;
  transition-delay: 1s;
  transition: 1s;
}

.inputContent {
  width: 90%;
  margin-top: 5%;
}
.changeInputButtons {
  margin-top: 2vh;
  display: flex;
  justify-content: space-between;
}

.changeInputButtons button {
  cursor: pointer;
  width: 50px;
  height: 20px;
  border: none;
  border-radius: 10px;
  box-shadow: 3px 3px 3px black;
  color: aliceblue;
  transition: 0.5s;
}
.changeInputButtons button:active {
  transform: scale(1.1, 1.1);
}

.changeInputButtons button:first-child {
  background-color: blue;
}
.changeInputButtons button:last-child {
  background-color: red;
}
